import { useFormContext } from 'react-hook-form';
import { FormField, FormItem, FormLabel, FormControl, FormMessage } from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import i18n from '@/lib/i18n/i18n';
import { cn } from '@/lib/utils';

interface TimeInputFormProps {
    className?: string;
    name: string;
    label?: string;
    placeholder?: string;
    disabled?: boolean;
}

export default function TimeInputForm({ className, name, label, placeholder, disabled }: TimeInputFormProps) {
    const { control } = useFormContext();
    return (
        <FormField
            control={control}
            name={name}
            render={({ field }) => (
                <FormItem className={className}>
                    {label && (
                        <FormLabel className="text-[#1A1F2C] font-medium text-[18px] leading-[33.73px] tracking-normal text-start">
                            {label}
                        </FormLabel>
                    )}
                    <FormControl>
                        <Input
                            {...field}
                            disabled={disabled}
                            type="time"
                            placeholder={placeholder}
                            className={cn('placeholder:text-[#8E9196] focus:border-[#9b87f5] focus:ring-[#9b87f5]', {
                                'justify-end': i18n.dir() === 'rtl',
                                'justify-start': i18n.dir() === 'ltr',
                            })}
                        />
                    </FormControl>
                    <FormMessage className="text-[#ea384c]" />
                </FormItem>
            )}
        />
    );
}
